import React, { useState } from 'react';

function HolidayModal({ isOpen, onClose, onConfirm }) {
  const [holidayData, setHolidayData] = useState({
    startDate: '',
    endDate: '',
    reason: ''
  });

  if (!isOpen) return null;

  const handleSubmit = (e) => {
    e.preventDefault();
    if (holidayData.startDate && holidayData.endDate && holidayData.reason) {
      onConfirm(holidayData);
      setHolidayData({ startDate: '', endDate: '', reason: '' });
    }
  };

  const handleClose = () => {
    setHolidayData({ startDate: '', endDate: '', reason: '' });
    onClose();
  };

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>임시 휴무일 추가</h3>
          <button className="modal-close" onClick={handleClose}>
            ×
          </button>
        </div>
        
        <form onSubmit={handleSubmit}>
          <div className="modal-body">
            <div className="form-group">
              <label>휴무 기간</label>
              <div className="date-range">
                <input
                  type="date"
                  value={holidayData.startDate}
                  onChange={(e) => setHolidayData({...holidayData, startDate: e.target.value})}
                  required
                />
                <span>~</span>
                <input
                  type="date"
                  value={holidayData.endDate}
                  onChange={(e) => setHolidayData({...holidayData, endDate: e.target.value})}
                  min={holidayData.startDate}
                  required
                />
              </div>
            </div>
            
            <div className="form-group">
              <label>휴무 사유</label>
              <input
                type="text"
                value={holidayData.reason}
                onChange={(e) => setHolidayData({...holidayData, reason: e.target.value})}
                placeholder="예: 여름 휴가"
                required
              />
            </div>
          </div>
          
          <div className="modal-footer">
            <button type="button" className="btn-cancel" onClick={handleClose}>
              취소
            </button>
            <button type="submit" className="btn-confirm">
              확인
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default HolidayModal;