import React from 'react';
import './PhotoModal.css';

function PhotoModal({ 
  isOpen, 
  photos, 
  selectedIndex, 
  mainPhotoId,
  onClose, 
  onPrevious, 
  onNext, 
  onSetMain 
}) {
  if (!isOpen || selectedIndex === null) return null;

  const currentPhoto = photos[selectedIndex];
  const isMainPhoto = mainPhotoId === currentPhoto.id;

  return (
    <div className="photo-modal-overlay" onClick={onClose}>
      <div className="photo-modal-content" onClick={(e) => e.stopPropagation()}>
        {/* 상단 헤더 영역 */}
        <div className="modal-header-area">
          <div className="modal-photo-counter">
            {selectedIndex + 1}/{photos.length}
          </div>
          
          <div className="modal-photo-name">
            {currentPhoto.name}
          </div>
          
          <button className="modal-close-btn" onClick={onClose}>
            ×
          </button>
        </div>
        
        {/* 이전 버튼 */}
        {selectedIndex > 0 && (
          <button className="modal-nav-btn prev" onClick={onPrevious}>
            ‹
          </button>
        )}
        
        {/* 사진 영역 */}
        <div className="modal-photo-wrapper">
          <img 
            src={currentPhoto.preview} 
            alt={currentPhoto.name}
            className="modal-photo"
          />
          
          {/* 대표 사진 설정 버튼 */}
          {isMainPhoto ? (
            <div className="modal-main-badge">
              ✓ 현재 대표 사진
            </div>
          ) : (
            <button 
              className="modal-set-main-btn"
              onClick={() => onSetMain(currentPhoto.id)}
            >
              대표 사진으로 설정
            </button>
          )}
        </div>
        
        {/* 다음 버튼 */}
        {selectedIndex < photos.length - 1 && (
          <button className="modal-nav-btn next" onClick={onNext}>
            ›
          </button>
        )}
      </div>
    </div>
  );
}

export default PhotoModal;