// src/pages/c-pay-02/c-pay-02.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import DateModal from '../../components/DateModal/DateModal';
import './c-pay-02.css';

function CPay02() {
  const navigate = useNavigate();
  
  // 파트너 타입 상태 관리
  const [partnerType, setPartnerType] = useState(localStorage.getItem('partnerType') || 'carCenter');
  
  // 현재 날짜 기준으로 최근 1년 설정
  const currentDate = new Date();
  const currentYear = currentDate.getFullYear();
  const currentMonth = currentDate.getMonth() + 1;
  
  // 기간 선택 상태
  const [dateRange, setDateRange] = useState({
    startYear: currentYear - 1,
    startMonth: currentMonth,
    endYear: currentYear,
    endMonth: currentMonth - 1 === 0 ? 12 : currentMonth - 1
  });
  
  // 모달 표시 여부
  const [showStartModal, setShowStartModal] = useState(false);
  const [showEndModal, setShowEndModal] = useState(false);

  // 데이터 상태
  const [performanceData, setPerformanceData] = useState([]);

  // localStorage 변경 감지 (파트너 타입 전환 시)
  useEffect(() => {
    const handleStorageChange = () => {
      const newType = localStorage.getItem('partnerType') || 'carCenter';
      if (newType !== partnerType) {
        setPartnerType(newType);
      }
    };

    // storage 이벤트 리스너 추가
    window.addEventListener('storage', handleStorageChange);
    
    // 같은 탭에서의 변경도 감지하기 위한 커스텀 이벤트
    window.addEventListener('partnerTypeChanged', handleStorageChange);
    
    return () => {
      window.removeEventListener('storage', handleStorageChange);
      window.removeEventListener('partnerTypeChanged', handleStorageChange);
    };
  }, [partnerType]);

  // 더미 데이터 생성 함수
  const generateDummyData = () => {
    const data = [];
    let year = dateRange.startYear;
    let month = dateRange.startMonth;
    
    while (true) {
      const monthStr = `${year}-${String(month).padStart(2, '0')}`;
      
      if (partnerType === 'carCenter') {
        // 카센터: 매출액 데이터
        const revenue = Math.floor(Math.random() * 1000000) + 1000000;
        data.push({
          month: monthStr,
          displayMonth: `${year}년 ${month}월`,
          value: revenue,
          unit: '원'
        });
      } else {
        // 검사소: 방문 건수 데이터
        const visits = Math.floor(Math.random() * 30) + 30;
        data.push({
          month: monthStr,
          displayMonth: `${year}년 ${month}월`,
          value: visits,
          unit: '건'
        });
      }
      
      // 종료 조건 체크
      if (year === dateRange.endYear && month === dateRange.endMonth) break;
      
      // 다음 달로 이동
      month++;
      if (month > 12) {
        month = 1;
        year++;
      }
    }
    
    return data.reverse(); // 최신 데이터가 위로 오도록
  };

    // 데이터 로드
    useEffect(() => {
        // 더미 데이터 생성 함수를 useEffect 내부로 이동
        console.log('Current partnerType:', partnerType);
        const generateData = () => {
          const data = [];
          let year = dateRange.startYear;
          let month = dateRange.startMonth;
          
          while (true) {
            const monthStr = `${year}-${String(month).padStart(2, '0')}`;
            
            if (partnerType === 'carCenter') {
              // 카센터: 매출액 데이터
              const revenue = Math.floor(Math.random() * 1000000) + 1000000;
              data.push({
                month: monthStr,
                displayMonth: `${year}년 ${month}월`,
                value: revenue,
                unit: '원'
              });
            } else {
              // 검사소: 방문 건수 데이터
              const visits = Math.floor(Math.random() * 30) + 30;
              data.push({
                month: monthStr,
                displayMonth: `${year}년 ${month}월`,
                value: visits,
                unit: '건'
              });
            }
            
            // 종료 조건 체크
            if (year === dateRange.endYear && month === dateRange.endMonth) break;
            
            // 다음 달로 이동
            month++;
            if (month > 12) {
              month = 1;
              year++;
            }
          }
          
          return data.reverse(); // 최신 데이터가 위로 오도록
        };
    
        setPerformanceData(generateData());
      }, [dateRange, partnerType]); // 의존성 배열에 필요한 값만 포함

  // 날짜 모달 열기
  const openDateModal = (type) => {
    if (type === 'start') {
      setShowStartModal(true);
    } else {
      setShowEndModal(true);
    }
  };

  // 시작 날짜 선택 완료
  const handleStartDateConfirm = (year, month) => {
    setDateRange(prev => ({
      ...prev,
      startYear: year,
      startMonth: month
    }));
    setShowStartModal(false);
  };

  // 종료 날짜 선택 완료
  const handleEndDateConfirm = (year, month) => {
    setDateRange(prev => ({
      ...prev,
      endYear: year,
      endMonth: month
    }));
    setShowEndModal(false);
  };

  // 조회 버튼 클릭
  const handleSearch = () => {
    // 날짜 유효성 검증
    const startDate = new Date(dateRange.startYear, dateRange.startMonth - 1);
    const endDate = new Date(dateRange.endYear, dateRange.endMonth - 1);
    
    if (startDate > endDate) {
      alert('시작일이 종료일보다 늦을 수 없습니다.');
      return;
    }
    
    // 2년 제한 검증
    const monthDiff = (endDate.getFullYear() - startDate.getFullYear()) * 12 + 
                      (endDate.getMonth() - startDate.getMonth());
    
    if (monthDiff > 24) {
      alert('최대 2년까지 조회 가능합니다.');
      return;
    }
    
    // 데이터 새로고침
    setPerformanceData(generateDummyData());
  };

  // 세부 조회 클릭
  const handleDetailView = (monthData) => {
    // c-pay-01로 이동하면서 해당 월 데이터 전달
    const [year, month] = monthData.month.split('-');
    navigate('/c-pay-01', { 
      state: { 
        year: parseInt(year), 
        month: parseInt(month) 
      } 
    });
  };

  // 금액 포맷팅
  const formatValue = (value) => {
    return value.toLocaleString('ko-KR');
  };

  return (
    <div className="performance-container">
      {/* 헤더 */}
      <div className="performance-header">
        <h2>{partnerType === 'carCenter' ? '매출 현황' : '방문 현황'}</h2>
      </div>

      {/* 기간 선택 */}
      <div className="date-range-selector">
        <div className="date-inputs">
          <span>기간 선택:</span>
          <button className="date-input" onClick={() => openDateModal('start')}>
            {dateRange.startYear}년 {String(dateRange.startMonth).padStart(2, '0')}월 ▼
          </button>
          <span>~</span>
          <button className="date-input" onClick={() => openDateModal('end')}>
            {dateRange.endYear}년 {String(dateRange.endMonth).padStart(2, '0')}월 ▼
          </button>
          <button className="search-btn" onClick={handleSearch}>
            조회
          </button>
        </div>
        <p className="date-info">
          * 최근 1년 데이터가 기본으로 조회되며, 최대 2년까지 조회 가능합니다.
        </p>
      </div>

      {/* 현황 테이블 */}
      <div className="performance-table">
        <table>
          <thead>
            <tr>
              <th>월</th>
              <th>{partnerType === 'carCenter' ? '매출액' : '방문완료 건수'}</th>
              <th>세부 조회</th>
            </tr>
          </thead>
          <tbody>
            {performanceData.length > 0 ? (
              performanceData.map((data, index) => (
                <tr key={index}>
                  <td>{data.displayMonth}</td>
                  <td className="value-cell">
                    {formatValue(data.value)} {data.unit}
                  </td>
                  <td>
                    <button 
                      className="detail-btn"
                      onClick={() => handleDetailView(data)}
                    >
                      세부 조회
                    </button>
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="3" className="empty-message">
                  조회된 데이터가 없습니다.
                </td>
              </tr>
            )}
          </tbody>
        </table>
      </div>

      {/* 시작 날짜 선택 모달 */}
      <DateModal
        isOpen={showStartModal}
        onClose={() => setShowStartModal(false)}
        onConfirm={handleStartDateConfirm}
        initialYear={dateRange.startYear}
        initialMonth={dateRange.startMonth}
        title="시작 날짜 선택"
        confirmText="선택"
        cancelText="취소"
      />

      {/* 종료 날짜 선택 모달 */}
      <DateModal
        isOpen={showEndModal}
        onClose={() => setShowEndModal(false)}
        onConfirm={handleEndDateConfirm}
        initialYear={dateRange.endYear}
        initialMonth={dateRange.endMonth}
        title="종료 날짜 선택"
        confirmText="선택"
        cancelText="취소"
      />
    </div>
  );
}

export default CPay02;