import React, { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import './c-log.css';

function CLog() {
  const [businessNumber, setBusinessNumber] = useState('');
  const [password, setPassword] = useState('');
  const [saveId, setSaveId] = useState(false);
  const navigate = useNavigate();

  // 사업자등록번호 포맷팅 (123-45-67890)
  const formatBusinessNumber = (value) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (numbers.length <= 3) return numbers;
    if (numbers.length <= 5) return `${numbers.slice(0, 3)}-${numbers.slice(3)}`;
    return `${numbers.slice(0, 3)}-${numbers.slice(3, 5)}-${numbers.slice(5, 10)}`;
  };

  const handleBusinessNumberChange = (e) => {
    const formatted = formatBusinessNumber(e.target.value);
    setBusinessNumber(formatted);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // TODO: 실제 로그인 처리
    console.log('로그인 시도:', { businessNumber, password, saveId });
    
    // 임시로 대시보드로 이동
    navigate('/c-home-01');
  };

  return (
    <div className="c-log-wrapper">
      <div className="c-log-container">
        <div className="c-log-logo">
          <h1>차바 파트너</h1>
        </div>

        <form className="c-log-form" onSubmit={handleSubmit}>
          <div className="c-log-input-group">
            <label>아이디 (사업자등록번호)</label>
            <input
              type="text"
              placeholder="123-45-67890"
              value={businessNumber}
              onChange={handleBusinessNumberChange}
              maxLength="12"
              required
            />
          </div>

          <div className="c-log-input-group">
            <label>비밀번호</label>
            <input
              type="password"
              placeholder="비밀번호를 입력하세요"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
          </div>

          <div className="c-log-checkbox">
            <input
              type="checkbox"
              id="saveId"
              checked={saveId}
              onChange={(e) => setSaveId(e.target.checked)}
            />
            <label htmlFor="saveId">아이디 저장</label>
          </div>

          <button type="submit" className="c-log-submit">
            로그인
          </button>
        </form>

        <div className="c-log-links">
          <Link to="/c-find">아이디/비밀번호 찾기</Link>
          <span className="c-log-divider">|</span>
          <Link to="/c-join">회원가입</Link>
        </div>
      </div>
    </div>
  );
}

export default CLog;