// src/pages/c-cont-02/c-cont-02-write.js
import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import './c-cont-02-write.css';

function CCont02Write() {
  const navigate = useNavigate();
  const location = useLocation();
  const editContent = location.state?.content;

  // 폼 데이터 상태
  const [formData, setFormData] = useState({
    type: 'notice',
    title: '',
    content: '',
    image: null
  });

  // 수정 모드인 경우 데이터 로드
  useEffect(() => {
    if (editContent) {
      setFormData({
        type: editContent.type,
        title: editContent.title,
        content: editContent.content || '',
        image: editContent.image || null
      });
    }
  }, [editContent]);

  // 입력값 변경 핸들러
  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // 이미지 업로드 핸들러
  const handleImageUpload = (e) => {
    const file = e.target.files[0];
    if (file) {
      // 파일 크기 체크 (5MB 제한)
      if (file.size > 5 * 1024 * 1024) {
        alert('파일 크기는 5MB 이하여야 합니다.');
        return;
      }

      // 이미지 파일 타입 체크
      if (!file.type.startsWith('image/')) {
        alert('이미지 파일만 업로드 가능합니다.');
        return;
      }

      setFormData(prev => ({
        ...prev,
        image: {
          name: file.name,
          url: URL.createObjectURL(file)
        }
      }));
    }
  };

  // 이미지 삭제
  const handleImageRemove = () => {
    setFormData(prev => ({
      ...prev,
      image: null
    }));
  };

  // 저장 핸들러
  const handleSave = () => {
    if (!formData.title.trim()) {
      alert('제목을 입력해주세요.');
      return;
    }

    if (!formData.content.trim()) {
      alert('내용을 입력해주세요.');
      return;
    }

    // 실제로는 API 호출
    alert(editContent ? '수정되었습니다.' : '저장되었습니다.');
    navigate('/c-cont-02');
  };

  return (
    <div className="content-write">
      <div className="write-header">
        <h2>{editContent ? '글 수정' : '새 글 작성'}</h2>
        <div className="header-buttons">
          <button 
            className="list-button"
            onClick={() => navigate('/c-cont-02')}
          >
            목록으로
          </button>
          <button 
            className="save-button"
            onClick={handleSave}
          >
            저장
          </button>
        </div>
      </div>

      <div className="write-form">
        {/* 콘텐츠 종류 선택 */}
        <div className="form-group">
          <label>콘텐츠 종류:</label>
          <div className="radio-group">
            <label className="radio-label">
              <input
                type="radio"
                name="type"
                value="notice"
                checked={formData.type === 'notice'}
                onChange={handleChange}
              />
              <span>공지</span>
            </label>
            <label className="radio-label">
              <input
                type="radio"
                name="type"
                value="posting"
                checked={formData.type === 'posting'}
                onChange={handleChange}
              />
              <span>포스팅</span>
            </label>
          </div>
        </div>

        {/* 제목 입력 */}
        <div className="form-group">
          <label htmlFor="title">제목:</label>
          <input
            type="text"
            id="title"
            name="title"
            value={formData.title}
            onChange={handleChange}
            placeholder="제목을 입력해주세요"
          />
        </div>

        {/* 내용 입력 */}
        <div className="form-group">
          <label htmlFor="content">내용:</label>
          <textarea
            id="content"
            name="content"
            value={formData.content}
            onChange={handleChange}
            placeholder="내용을 입력해주세요"
            rows="12"
          />
        </div>

        {/* 이미지 첨부 (포스팅 전용) */}
        {formData.type === 'posting' && (
          <div className="form-group">
            <label>이미지 첨부 (포스팅 전용):</label>
            <div className="image-upload">
              {!formData.image ? (
                <label className="upload-button">
                  + 파일 선택
                  <input
                    type="file"
                    accept="image/*"
                    onChange={handleImageUpload}
                    hidden
                  />
                </label>
              ) : (
                <div className="uploaded-image">
                  <span className="image-name">{formData.image.name}</span>
                  <button 
                    className="remove-button"
                    onClick={handleImageRemove}
                  >
                    ✕
                  </button>
                </div>
              )}
            </div>
            {formData.image && (
              <div className="image-preview">
                <img src={formData.image.url} alt="미리보기" />
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}

export default CCont02Write;