import React, { useState, useMemo } from 'react';
import DateModal from '../../components/DateModal/DateModal';
import './c-res-01-02.css';

function CRes0102() {
  const [selectedDateType, setSelectedDateType] = useState('request');
  const [searchType, setSearchType] = useState('all');
  const [searchKeyword, setSearchKeyword] = useState('');
  const [startDate, setStartDate] = useState({ year: 2025, month: 7, day: 1 });
  const [endDate, setEndDate] = useState({ year: 2025, month: 7, day: 31 });
  const [showDateModal, setShowDateModal] = useState(false);
  const [dateModalType, setDateModalType] = useState('start');
  const [sortConfig, setSortConfig] = useState({ key: null, direction: 'asc' });

  // 검사 대행 데이터만 추출 (방문 예약 관리에서 inspectionType이 '검사대행'인 것만)
  const [delegations, setDelegations] = useState([
    {
      id: 2,
      status: '대행요청', // 예약대기를 대행요청으로 변경
      requestDate: '2025-07-02',
      displayDate: '07.02(수)',
      time: '오전 10:00',
      vehicleNumber: '32머6936',
      phone: '010-4952-7782',
      vehicleType: '소형',
      carCenter: '우리카센터',
      visitStatus: '대기중'
    },
    {
      id: 3,
      status: '예약확정',
      requestDate: '2025-07-02',
      displayDate: '07.02(수)',
      time: '오전 11:00',
      vehicleNumber: '15마8293',
      phone: '010-6527-2889',
      vehicleType: '소형',
      carCenter: '베스트모터스',
      visitStatus: '대기중'
    },
    {
      id: 4,
      status: '대행취소', // 예약취소를 대행취소로 변경
      requestDate: '2025-07-02',
      displayDate: '07.02(수)',
      time: '오전 11:00',
      vehicleNumber: '20부2833',
      phone: '010-4184-0842',
      vehicleType: '소형',
      carCenter: '한국자동차정비',
      visitStatus: '취소'
    },
    {
      id: 6,
      status: '검사완료', // 방문완료를 검사완료로 변경
      requestDate: '2025-07-03',
      displayDate: '07.03(목)',
      time: '오전 9:00',
      vehicleNumber: '32머6936',
      phone: '010-4952-7782',
      vehicleType: '소형',
      carCenter: '우리카센터',
      visitStatus: '완료'
    },
    {
      id: 8,
      status: '검사완료',
      requestDate: '2025-07-03',
      displayDate: '07.03(목)',
      time: '오후 2:00',
      vehicleNumber: '47루8923',
      phone: '010-5062-8512',
      vehicleType: '소형',
      carCenter: '스마트정비',
      visitStatus: '완료'
    },
    {
      id: 10,
      status: '대행요청',
      requestDate: '2025-07-05',
      displayDate: '07.05(토)',
      time: '오전 9:00',
      vehicleNumber: '09우9245',
      phone: '010-2557-3005',
      vehicleType: '소형',
      carCenter: '모터스원',
      visitStatus: '대기중'
    }
  ]);

  // 새로운 대행 요청 개수 계산
  const newRequestCount = useMemo(() => {
    return delegations.filter(d => d.status === '대행요청').length;
  }, [delegations]);

  // 버튼 액션 핸들러
  const handleConfirm = (id) => {
    setDelegations(prev => prev.map(del => 
      del.id === id 
        ? { ...del, status: '예약확정' }
        : del
    ));
  };

  const handleReject = (id) => {
    setDelegations(prev => prev.map(del => 
      del.id === id 
        ? { ...del, status: '대행취소', visitStatus: '취소' }
        : del
    ));
  };

  const handleComplete = (id) => {
    setDelegations(prev => prev.map(del => 
      del.id === id 
        ? { ...del, status: '검사완료', visitStatus: '완료' }
        : del
    ));
  };

  const handleCancel = (id) => {
    setDelegations(prev => prev.map(del => 
      del.id === id 
        ? { ...del, status: '대행취소', visitStatus: '취소' }
        : del
    ));
  };

  // 정렬 함수
  const handleSort = (key) => {
    let direction = 'asc';
    if (sortConfig.key === key && sortConfig.direction === 'asc') {
      direction = 'desc';
    }
    setSortConfig({ key, direction });
  };

  // 정렬된 데이터
  const sortedDelegations = useMemo(() => {
    let sortableItems = [...delegations];
    
    if (sortConfig.key) {
      sortableItems.sort((a, b) => {
        let aValue = a[sortConfig.key];
        let bValue = b[sortConfig.key];
        
        if (sortConfig.key === 'status') {
          const statusOrder = { 
            '대행요청': 1, 
            '예약확정': 2, 
            '검사완료': 3,
            '대행취소': 4
          };
          aValue = statusOrder[aValue] || 999;
          bValue = statusOrder[bValue] || 999;
        }
        
        if (aValue < bValue) {
          return sortConfig.direction === 'asc' ? -1 : 1;
        }
        if (aValue > bValue) {
          return sortConfig.direction === 'asc' ? 1 : -1;
        }
        return 0;
      });
    }
    
    return sortableItems;
  }, [delegations, sortConfig]);

  const handleDateSelect = (year, month, day) => {
    if (dateModalType === 'start') {
      setStartDate({ year, month, day });
    } else {
      setEndDate({ year, month, day });
    }
    setShowDateModal(false);
  };

  const getStatusColor = (status) => {
    switch (status) {
      case '대행요청': return '#ffc107';
      case '예약확정': return '#28a745';
      case '검사완료': return '#007bff';
      case '대행취소': return '#dc3545';
      default: return '#000';
    }
  };

  const getSortIcon = (columnKey) => {
    if (sortConfig.key !== columnKey) {
      return <span className="sort-icon">↕</span>;
    }
    return sortConfig.direction === 'asc' 
      ? <span className="sort-icon active">↑</span> 
      : <span className="sort-icon active">↓</span>;
  };

  return (
    <div className="c-res-01-02">
      <div className="page-header">
        <h1>검사 대행 관리</h1>
        <span className="new-indicator">NEW</span>
      </div>

      {/* 검색 필터 */}
      <div className="search-filters">
        <div className="filter-row">
          <div className="filter-group">
            <select 
              value={selectedDateType} 
              onChange={(e) => setSelectedDateType(e.target.value)}
              className="date-type-select"
            >
              <option value="request">요청일</option>
              <option value="visit">대행일</option>
            </select>
          </div>

          <div className="date-range">
            <button 
              className="date-button"
              onClick={() => {
                setDateModalType('start');
                setShowDateModal(true);
              }}
            >
              {startDate.year}.{String(startDate.month).padStart(2, '0')}.{String(startDate.day).padStart(2, '0')}
            </button>
            <span className="date-separator">~</span>
            <button 
              className="date-button"
              onClick={() => {
                setDateModalType('end');
                setShowDateModal(true);
              }}
            >
              {endDate.year}.{String(endDate.month).padStart(2, '0')}.{String(endDate.day).padStart(2, '0')}
            </button>
          </div>

          <div className="filter-group">
            <select 
              value={searchType}
              onChange={(e) => setSearchType(e.target.value)}
              className="search-type-select"
            >
              <option value="all">전체</option>
              <option value="vehicle">차량번호</option>
              <option value="phone">전화번호</option>
              <option value="carCenter">카센터명</option>
            </select>
          </div>

          <div className="search-input-group">
            <input
              type="text"
              placeholder="검색어를 입력하세요..."
              value={searchKeyword}
              onChange={(e) => setSearchKeyword(e.target.value)}
              className="search-input"
            />
            <button className="search-button">검색</button>
          </div>
        </div>
      </div>

      {/* 대행 요청 알림 */}
      {newRequestCount > 0 && (
        <div className="delegation-alert">
          <span className="alert-icon">🔔</span>
          <span className="alert-text">
            새로운 검사 대행 요청이 {newRequestCount}건 있습니다.
          </span>
          <button className="alert-action">확인하기</button>
        </div>
      )}

      {/* 대행 목록 테이블 */}
      <div className="delegation-table">
        <table>
          <thead>
            <tr>
              <th onClick={() => handleSort('status')} className="sortable">
                상태 {getSortIcon('status')}
              </th>
              <th onClick={() => handleSort('requestDate')} className="sortable">
                요청일자 {getSortIcon('requestDate')}
              </th>
              <th onClick={() => handleSort('time')} className="sortable">
                예약시간 {getSortIcon('time')}
              </th>
              <th>차량번호</th>
              <th>전화번호</th>
              <th>차종</th>
              <th onClick={() => handleSort('carCenter')} className="sortable">
                카센터 {getSortIcon('carCenter')}
              </th>
              <th>관리</th>
            </tr>
          </thead>
          <tbody>
            {sortedDelegations.map((delegation) => (
              <tr key={delegation.id}>
                <td>
                  <span 
                    className="status-badge"
                    style={{ color: getStatusColor(delegation.status) }}
                  >
                    ● {delegation.status}
                  </span>
                </td>
                <td>{delegation.displayDate}</td>
                <td>{delegation.time}</td>
                <td>{delegation.vehicleNumber}</td>
                <td>{delegation.phone}</td>
                <td>{delegation.vehicleType}</td>
                <td>{delegation.carCenter}</td>
                <td>
                  {delegation.status === '대행요청' && (
                    <div className="action-buttons">
                      <button 
                        className="action-btn confirm"
                        onClick={() => handleConfirm(delegation.id)}
                      >
                        확정
                      </button>
                      <button 
                        className="action-btn reject"
                        onClick={() => handleReject(delegation.id)}
                      >
                        거절
                      </button>
                    </div>
                  )}
                  {delegation.status === '예약확정' && (
                    <div className="action-buttons">
                      <button 
                        className="action-btn complete"
                        onClick={() => handleComplete(delegation.id)}
                      >
                        완료
                      </button>
                      <button 
                        className="action-btn cancel"
                        onClick={() => handleCancel(delegation.id)}
                      >
                        취소
                      </button>
                    </div>
                  )}
                  {(delegation.status === '검사완료' || delegation.status === '대행취소') && (
                    <span className="completed-text">처리완료</span>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/* 페이지네이션 */}
      <div className="pagination">
        <button className="page-btn">&lt;</button>
        <button className="page-btn active">1</button>
        <button className="page-btn">2</button>
        <button className="page-btn">3</button>
        <button className="page-btn">&gt;</button>
      </div>

      {/* 날짜 선택 모달 */}
      <DateModal
        isOpen={showDateModal}
        onClose={() => setShowDateModal(false)}
        onConfirm={handleDateSelect}
        initialDate={dateModalType === 'start' ? startDate : endDate}
        title="날짜 선택"
        mode="day"
      />
    </div>
  );
}

export default CRes0102;