import React, { useState, useEffect } from 'react';
import HolidayModal from './HolidayModal';

function OperationTab({ onDataChange }) {
  const [partnerType, setPartnerType] = useState(
    localStorage.getItem('partnerType') || 'carCenter'
  );

  const [businessHours, setBusinessHours] = useState({
    monday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    tuesday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    wednesday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    thursday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    friday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    saturday: { enabled: true, open: '09:00', close: '13:00', lunch: null },
    sunday: { enabled: false }
  });

  // 검사 대행 가능 시간 (카센터 전용)
  const [inspectionHours, setInspectionHours] = useState({
    monday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    tuesday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    wednesday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    thursday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    friday: { enabled: true, open: '09:00', close: '18:00', lunch: { start: '12:00', end: '13:00' } },
    saturday: { enabled: true, open: '09:00', close: '13:00', lunch: null },
    sunday: { enabled: false }
  });

  const [holidaySettings, setHolidaySettings] = useState({
    legalHolidays: true,
    temporaryHolidays: [
      { startDate: '2025-08-11', endDate: '2025-08-15', reason: '여름 정기 휴가' },
      { startDate: '2025-10-09', endDate: '2025-10-09', reason: '워크샵' }
    ]
  });

  const [showHolidayModal, setShowHolidayModal] = useState(false);

  // 파트너 타입 변경 감지
  useEffect(() => {
    const handleStorageChange = () => {
      const newType = localStorage.getItem('partnerType') || 'carCenter';
      setPartnerType(newType);
    };

    window.addEventListener('storage', handleStorageChange);
    window.addEventListener('partnerTypeChanged', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
      window.removeEventListener('partnerTypeChanged', handleStorageChange);
    };
  }, []);

  const handleHoursChange = (day, field, value) => {
    setBusinessHours(prev => ({
      ...prev,
      [day]: { ...prev[day], [field]: value }
    }));
    if (onDataChange) onDataChange();
  };

  const handleInspectionHoursChange = (day, field, value) => {
    setInspectionHours(prev => ({
      ...prev,
      [day]: { ...prev[day], [field]: value }
    }));
    if (onDataChange) onDataChange();
  };

  const handleAddHoliday = () => {
    setShowHolidayModal(true);
  };

  const handleHolidayConfirm = (holidayData) => {
    setHolidaySettings(prev => ({
      ...prev,
      temporaryHolidays: [...prev.temporaryHolidays, holidayData]
    }));
    setShowHolidayModal(false);
    if (onDataChange) onDataChange();
  };

  const handleDeleteHoliday = (index) => {
    if (window.confirm('이 휴무일을 삭제하시겠습니까?')) {
      setHolidaySettings(prev => ({
        ...prev,
        temporaryHolidays: prev.temporaryHolidays.filter((_, i) => i !== index)
      }));
      if (onDataChange) onDataChange();
    }
  };

  const dayNames = {
    monday: '월요일',
    tuesday: '화요일',
    wednesday: '수요일',
    thursday: '목요일',
    friday: '금요일',
    saturday: '토요일',
    sunday: '일요일'
  };

  // 시간 설정 렌더링 함수
  const renderHoursSection = (hours, handleChange, title) => (
    <>
      <h3 className="section-title">{title}</h3>
      {Object.entries(hours).map(([day, dayHours]) => (
        <div key={day} className="hours-row">
          <label className="day-label">
            <input
              type="checkbox"
              checked={dayHours.enabled}
              onChange={(e) => handleChange(day, 'enabled', e.target.checked)}
            />
            {dayNames[day]}
          </label>

          {dayHours.enabled && (
            <div className="time-inputs">
              <input
                type="time"
                value={dayHours.open}
                onChange={(e) => handleChange(day, 'open', e.target.value)}
              />
              <span>~</span>
              <input
                type="time"
                value={dayHours.close}
                onChange={(e) => handleChange(day, 'close', e.target.value)}
              />
              
              {dayHours.lunch && (
                <>
                  <span className="lunch-label">점심</span>
                  <input
                    type="time"
                    value={dayHours.lunch.start}
                    onChange={(e) => handleChange(day, 'lunch', { ...dayHours.lunch, start: e.target.value })}
                  />
                  <span>~</span>
                  <input
                    type="time"
                    value={dayHours.lunch.end}
                    onChange={(e) => handleChange(day, 'lunch', { ...dayHours.lunch, end: e.target.value })}
                  />
                </>
              )}
            </div>
          )}

          {!dayHours.enabled && <span className="closed-text">휴무</span>}
        </div>
      ))}
    </>
  );

  return (
    <div className="operation-tab">
      {/* 영업시간 설정 */}
      {renderHoursSection(businessHours, handleHoursChange, '영업시간 설정')}

      {/* 카센터인 경우에만 검사 대행 가능 시간 표시 */}
      {partnerType === 'carCenter' && (
        <>
          <hr className="section-divider" />
          {renderHoursSection(inspectionHours, handleInspectionHoursChange, '검사 대행 가능 시간 설정')}
        </>
      )}

      <hr className="section-divider" />

      <h3 className="section-title">공휴일 설정</h3>
      
      <div className="form-group">
        <label>
          <input
            type="checkbox"
            checked={holidaySettings.legalHolidays}
            onChange={(e) => {
              setHolidaySettings(prev => ({ ...prev, legalHolidays: e.target.checked }));
              if (onDataChange) onDataChange();
            }}
          />
          법정 공휴일 전체 휴무 (근로자의 날 포함)
        </label>
      </div>

      <hr className="section-divider" />

      <h3 className="section-title">임시 휴무일 관리</h3>

      <button 
        className="btn-secondary" 
        onClick={handleAddHoliday}
        type="button"
      >
        + 임시 휴무일 추가
      </button>

      <div className="holiday-tabs">
        <button className="tab-button active">
          예정된 휴무 ({holidaySettings.temporaryHolidays.length})
        </button>
        <button className="tab-button">지난 휴무 (5)</button>
      </div>

      <div className="holiday-list">
        {holidaySettings.temporaryHolidays.map((holiday, index) => (
          <div key={index} className="holiday-item">
            <span>{holiday.startDate} ~ {holiday.endDate}</span>
            <span className="holiday-reason">{holiday.reason}</span>
            <button 
              className="btn-delete" 
              onClick={() => handleDeleteHoliday(index)}
              type="button"
            >
              삭제 X
            </button>
          </div>
        ))}
      </div>

      <HolidayModal
        isOpen={showHolidayModal}
        onClose={() => setShowHolidayModal(false)}
        onConfirm={handleHolidayConfirm}
      />
    </div>
  );
}

export default OperationTab;