import React, { useState } from 'react';
import { DISCOUNT_POLICIES } from '../../data/discountData';
import './DiscountPolicy.css';

function DiscountPolicy({ onSave }) {
  const [policies, setPolicies] = useState(DISCOUNT_POLICIES);
  const [expandedCard, setExpandedCard] = useState(null);

  const handleToggle = (policyId) => {
    const updatedPolicies = policies.map(policy => 
      policy.id === policyId 
        ? { ...policy, enabled: !policy.enabled }
        : policy
    );
    setPolicies(updatedPolicies);
    // 상위 컴포넌트로 변경사항 전달
    onSave(updatedPolicies);
  };

  const handleCardClick = (policyId) => {
    setExpandedCard(expandedCard === policyId ? null : policyId);
  };

  return (
    <div className="discount-policy-container">
      <h2 className="section-title">묶음 할인 설정</h2>
      <p className="section-description">
        * 고객의 객단가를 높일 수 있는 자동 할인 정책을 선택(On/Off)하세요.
      </p>

      <div className="policy-cards">
        {policies.map(policy => (
          <div 
            key={policy.id} 
            className={`policy-card ${policy.enabled ? 'enabled' : ''} ${expandedCard === policy.id ? 'expanded' : ''}`}
          >
            <div className="policy-header" onClick={() => handleCardClick(policy.id)}>
              <div className="policy-info">
                <span className="policy-type">[{policy.type}]</span>
                <span className="policy-items">
                  {policy.items.join(' + ')} 
                </span>
                <span className="policy-discount">
                  ▶ {policy.discount.toLocaleString()}원 할인
                </span>
              </div>
              
              <div className="policy-toggle" onClick={(e) => e.stopPropagation()}>
                <input
                  type="radio"
                  id={`${policy.id}-on`}
                  name={policy.id}
                  checked={policy.enabled}
                  onChange={() => handleToggle(policy.id)}
                />
                <label htmlFor={`${policy.id}-on`} className="toggle-label">On</label>
                
                <input
                  type="radio"
                  id={`${policy.id}-off`}
                  name={policy.id}
                  checked={!policy.enabled}
                  onChange={() => handleToggle(policy.id)}
                />
                <label htmlFor={`${policy.id}-off`} className="toggle-label">Off</label>
              </div>
            </div>
            
            {expandedCard === policy.id && (
              <div className="policy-description">
                <div className="description-divider"></div>
                <p>{policy.description}</p>
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

export default DiscountPolicy;