import React from 'react';
import { HashRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Layout from './components/Layout/Layout';
import ErrorBoundary from './components/ErrorBoundary';
import CLog from './pages/c-log/c-log';
import CJoin from './pages/c-join/c-join';
import CHome01 from './pages/c-home-01/c-home-01';
import CRes0101 from './pages/c-res-01-01/c-res-01-01'; 
import CRes0102 from './pages/c-res-01-02/c-res-01-02'; 
import CRes0201 from './pages/c-res-02-01/c-res-02-01';
import CStore01 from './pages/c-store-01/c-store-01';
import CStore02 from './pages/c-store-02/c-store-02';
import CStore03 from './pages/c-store-03/c-store-03';
import CCont02 from './pages/c-cont-02/c-cont-02';
import CCont02Write from './pages/c-cont-02/c-cont-02-write';
import CPay01 from './pages/c-pay-01/c-pay-01';
import CPay02 from './pages/c-pay-02/c-pay-02';
import CCs02 from './pages/c-cs-02/c-cs-02';

function App() {
  return (
    <ErrorBoundary>
      <Router>
        <Routes>
          <Route path="/" element={<Layout />}>
            <Route index element={<Navigate to="/c-log" replace />} />
            <Route path="c-log" element={<CLog />} />
            <Route path="c-join" element={<CJoin />} />
            <Route path="c-home-01" element={<CHome01 />} />
            <Route path="c-res-01-01" element={<CRes0101 />} />
            <Route path="c-res-01-02" element={<CRes0102 />} />
            <Route path="c-res-02-01" element={<CRes0201 />} />
            <Route path="c-store-01" element={<CStore01 />} />
            <Route path="c-store-02" element={<CStore02 />} />
            <Route path="c-store-03" element={<CStore03 />} />
            <Route path="c-cont-02" element={<CCont02 />} />
            <Route path="c-cont-02-write" element={<CCont02Write />} />
            <Route path="c-pay-01" element={<CPay01 />} />
            <Route path="c-pay-02" element={<CPay02 />} />
            <Route path="c-cs-02" element={<CCs02 />} />
          </Route>
        </Routes>
      </Router>
    </ErrorBoundary>
  );
}

export default App;