import React from 'react';

function SaveConfirmModal({ isOpen, onCancel, onConfirm, onSkip }) {
  if (!isOpen) return null;

  return (
    <div className="save-modal-overlay">
      <div className="save-modal-content">
        <div className="save-modal-body">
          <p>저장하지 않은 변경사항이 있습니다.</p>
          <p>저장하시겠습니까?</p>
        </div>
        <div className="save-modal-footer">
          <button className="btn-cancel" onClick={onCancel}>취소</button>
          <button className="btn-skip" onClick={onSkip}>저장하지 않고 이동</button>
          <button className="btn-save" onClick={onConfirm}>저장 후 이동</button>
        </div>
      </div>
    </div>
  );
}

export default SaveConfirmModal;