import React, { useState, useCallback, useMemo } from 'react';
import CertificateModal from './CertificateModal';
import SNSModal from './SNSModal';
import PhotoModal from './PhotoModal';

// 상수 분리
const INITIAL_FORM_DATA = {
  introduction: '',
  photos: [],
  union: '',
  certificates: [],
  specialties: [],
  additionalServices: [],
  sns: []
};

const SPECIALTY_TAGS = ['수입차', '엔진', '전기/전자장치', '타이어', '정비'];
const DEFAULT_SERVICES = ['워셔액교체', '부동액교체', '타이어공기압'];
const MAX_PHOTOS = 5;
const MAX_INTRODUCTION_LENGTH = 2000;

function ProfileTab({ onDataChange }) {
  // State 관리
  const [formData, setFormData] = useState(INITIAL_FORM_DATA);
  const [selectedPhotoIndex, setSelectedPhotoIndex] = useState(null);
  const [mainPhotoId, setMainPhotoId] = useState(null);
  const [customServices, setCustomServices] = useState([]);
  const [availableDefaultServices, setAvailableDefaultServices] = useState(DEFAULT_SERVICES);
  const [showCertificateModal, setShowCertificateModal] = useState(false);
  const [showSNSModal, setShowSNSModal] = useState(false);

  // 공통 핸들러
  const handleChange = useCallback((field, value) => {
    setFormData(prev => ({ ...prev, [field]: value }));
    onDataChange();
  }, [onDataChange]);

  // LNB 프로필 업데이트
  const updateLNBProfile = useCallback((photoPreview) => {
    if (photoPreview) {
      localStorage.setItem('mainPhotoPreview', photoPreview);
    } else {
      localStorage.removeItem('mainPhotoPreview');
    }
    window.dispatchEvent(new Event('mainPhotoChanged'));
  }, []);

  // 사진 관련 함수들
  const handlePhotoUpload = useCallback((e) => {
    const files = Array.from(e.target.files);
    const remainingSlots = MAX_PHOTOS - formData.photos.length;
    
    if (files.length > remainingSlots) {
      alert(`사진은 최대 ${MAX_PHOTOS}장까지 등록 가능합니다. ${remainingSlots}장만 추가됩니다.`);
      files.splice(remainingSlots);
    }
    
    const newPhotos = files.map((file, index) => ({
      file,
      name: file.name,
      preview: URL.createObjectURL(file),
      id: Date.now() + index
    }));
    
    // 첫 사진을 대표 사진으로 설정
    if (formData.photos.length === 0 && newPhotos.length > 0) {
      setMainPhotoId(newPhotos[0].id);
      updateLNBProfile(newPhotos[0].preview);
    }
    
    handleChange('photos', [...formData.photos, ...newPhotos]);
    e.target.value = ''; // input 초기화
  }, [formData.photos, handleChange, updateLNBProfile]);

  const handlePhotoDelete = useCallback((photoId) => {
    const photoToDelete = formData.photos.find(photo => photo.id === photoId);
    if (photoToDelete?.preview) {
      URL.revokeObjectURL(photoToDelete.preview);
    }
    
    const newPhotos = formData.photos.filter(photo => photo.id !== photoId);
    
    // 대표 사진이 삭제된 경우 처리
    if (mainPhotoId === photoId) {
      if (newPhotos.length > 0) {
        setMainPhotoId(newPhotos[0].id);
        updateLNBProfile(newPhotos[0].preview);
      } else {
        setMainPhotoId(null);
        updateLNBProfile(null);
      }
    }
    
    handleChange('photos', newPhotos);
  }, [formData.photos, mainPhotoId, handleChange, updateLNBProfile]);

  // 사진 모달 핸들러들
  const handleModalPrevious = useCallback(() => {
    setSelectedPhotoIndex(prev => (prev > 0 ? prev - 1 : prev));
  }, []);

  const handleModalNext = useCallback(() => {
    setSelectedPhotoIndex(prev => 
      (prev < formData.photos.length - 1 ? prev + 1 : prev)
    );
  }, [formData.photos.length]);

  const handleSetMainPhoto = useCallback((photoId) => {
    setMainPhotoId(photoId);
    const mainPhoto = formData.photos.find(p => p.id === photoId);
    if (mainPhoto) {
      updateLNBProfile(mainPhoto.preview);
      alert('대표 사진으로 설정되었습니다.');
    }
  }, [formData.photos, updateLNBProfile]);

  // 자격증 관련 함수들
  const handleCertificateConfirm = useCallback((certificateData) => {
    const newCertificate = {
      id: Date.now(),
      ...certificateData
    };
    handleChange('certificates', [...formData.certificates, newCertificate]);
    setShowCertificateModal(false);
  }, [formData.certificates, handleChange]);

  const handleDeleteCertificate = useCallback((certId) => {
    handleChange('certificates', formData.certificates.filter(cert => cert.id !== certId));
  }, [formData.certificates, handleChange]);

  // SNS 관련 함수들
  const handleSNSConfirm = useCallback((snsData) => {
    const newSNS = {
      id: Date.now(),
      ...snsData
    };
    handleChange('sns', [...formData.sns, newSNS]);
    setShowSNSModal(false);
  }, [formData.sns, handleChange]);

  const handleDeleteSNS = useCallback((snsId) => {
    handleChange('sns', formData.sns.filter(sns => sns.id !== snsId));
  }, [formData.sns, handleChange]);

  const handleSNSClick = useCallback((url, e) => {
    e.preventDefault();
    window.open(url, '_blank', 'noopener,noreferrer');
  }, []);

  // 전문분야 토글
  const toggleSpecialty = useCallback((tag) => {
    const newSpecialties = formData.specialties.includes(tag)
      ? formData.specialties.filter(t => t !== tag)
      : [...formData.specialties, tag];
    handleChange('specialties', newSpecialties);
  }, [formData.specialties, handleChange]);

  // 부가서비스 관련 함수들
  const handleAddCustomService = useCallback((e) => {
    if (e.key === 'Enter') {
      const newService = e.target.value.trim();
      if (newService && !customServices.includes(newService)) {
        setCustomServices(prev => [...prev, newService]);
        handleChange('additionalServices', [...formData.additionalServices, newService]);
        e.target.value = '';
      }
    }
  }, [customServices, formData.additionalServices, handleChange]);

  const toggleService = useCallback((tag) => {
    const newServices = formData.additionalServices.includes(tag)
      ? formData.additionalServices.filter(t => t !== tag)
      : [...formData.additionalServices, tag];
    handleChange('additionalServices', newServices);
  }, [formData.additionalServices, handleChange]);

  const removeDefaultService = useCallback((tag, e) => {
    e.stopPropagation();
    setAvailableDefaultServices(prev => prev.filter(s => s !== tag));
    handleChange('additionalServices', formData.additionalServices.filter(t => t !== tag));
  }, [formData.additionalServices, handleChange]);

  const removeCustomService = useCallback((service, e) => {
    e.stopPropagation();
    setCustomServices(prev => prev.filter(s => s !== service));
    handleChange('additionalServices', formData.additionalServices.filter(t => t !== service));
  }, [formData.additionalServices, handleChange]);

  // 계산된 값들
  const photoUploadDisabled = useMemo(() => 
    formData.photos.length >= MAX_PHOTOS, 
    [formData.photos.length]
  );

  return (
    <div className="profile-tab">
      {/* 소개글 */}
      <div className="form-group">
        <label>소개글</label>
        <textarea
          value={formData.introduction}
          onChange={(e) => handleChange('introduction', e.target.value)}
          placeholder="이제 동네에서 가장 믿을 수 있는 저희 카센터에서..."
          rows="4"
          maxLength={MAX_INTRODUCTION_LENGTH}
        />
        <div className="help-text">
          {formData.introduction.length}/{MAX_INTRODUCTION_LENGTH}
        </div>
      </div>

      {/* 사진 업로드 */}
      <div className="form-group">
        <label>사진 (최대 {MAX_PHOTOS}장)</label>
        <div className="photo-upload-area">
          <input
            type="file"
            id="photo-upload"
            multiple
            accept="image/*"
            onChange={handlePhotoUpload}
            disabled={photoUploadDisabled}
            style={{ display: 'none' }}
          />
          <label 
            htmlFor="photo-upload" 
            className={`btn-secondary ${photoUploadDisabled ? 'disabled' : ''}`}
          >
            + 사진추가
          </label>
          <span className="help-text">
            {formData.photos.length}/{MAX_PHOTOS}
          </span>
        </div>
        
        {formData.photos.length > 0 && (
          <>
            <div className="photo-preview-container">
              {formData.photos.map((photo, index) => (
                <div 
                  key={photo.id} 
                  className={`photo-item ${mainPhotoId === photo.id ? 'main-photo' : ''}`}
                  onClick={() => setSelectedPhotoIndex(index)}
                >
                  {mainPhotoId === photo.id && (
                    <div className="main-photo-badge">대표</div>
                  )}
                  <img src={photo.preview} alt={photo.name} />
                  <div className="photo-name" title={photo.name}>
                    {photo.name}
                  </div>
                  <button 
                    className="photo-delete-btn"
                    onClick={(e) => {
                      e.stopPropagation();
                      handlePhotoDelete(photo.id);
                    }}
                    type="button"
                    aria-label="사진 삭제"
                  >
                    ×
                  </button>
                </div>
              ))}
            </div>
            <div className="photo-help-text">
              사진을 클릭하여 크게 보고 대표 사진을 설정할 수 있습니다.
            </div>
          </>
        )}
      </div>

      {/* 소속 조합 */}
      <div className="form-group">
        <label>소속 조합</label>
        <select
          value={formData.union}
          onChange={(e) => handleChange('union', e.target.value)}
        >
          <option value="">없음</option>
          <option value="카포스">카포스</option>
        </select>
      </div>

      {/* 자격증 */}
      <div className="form-group">
        <label>자격증</label>
        <div className="button-group">
          <button 
            className="btn-secondary" 
            onClick={() => setShowCertificateModal(true)} 
            type="button"
          >
            + 자격증 추가
          </button>
        </div>
        {formData.certificates.length > 0 && (
          <div className="certificate-list">
            {formData.certificates.map((cert) => (
              <div key={cert.id} className="certificate-item">
                <span>{cert.name} - {cert.number}</span>
                <button 
                  onClick={() => handleDeleteCertificate(cert.id)}
                  className="btn-delete"
                  type="button"
                >
                  삭제 ×
                </button>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* 전문분야 */}
      <div className="form-group">
        <label>전문분야</label>
        <div className="tag-container">
          {SPECIALTY_TAGS.map(tag => (
            <button
              key={tag}
              type="button"
              className={`tag ${formData.specialties.includes(tag) ? 'active' : ''}`}
              onClick={() => toggleSpecialty(tag)}
            >
              #{tag}
            </button>
          ))}
        </div>
      </div>

      {/* 부가서비스 */}
      <div className="form-group">
        <label>부가서비스</label>
        <div className="tag-container">
          {availableDefaultServices.map(tag => (
            <button
              key={tag}
              type="button"
              className={`tag with-delete ${formData.additionalServices.includes(tag) ? 'active' : ''}`}
              onClick={() => toggleService(tag)}
            >
              #{tag}
              <span className="delete-x" onClick={(e) => removeDefaultService(tag, e)}>×</span>
            </button>
          ))}
          
          {customServices.map(service => (
            <button
              key={service}
              type="button"
              className={`tag with-delete ${formData.additionalServices.includes(service) ? 'active' : ''}`}
              onClick={() => toggleService(service)}
            >
              #{service}
              <span className="delete-x" onClick={(e) => removeCustomService(service, e)}>×</span>
            </button>
          ))}
          
          <input
            type="text"
            placeholder="부가서비스 추가"
            className="tag-input"
            onKeyPress={handleAddCustomService}
          />
        </div>
      </div>

      {/* SNS 등록 */}
      <div className="form-group">
        <label>SNS 등록</label>
        <div className="button-group">
          <button 
            className="btn-secondary" 
            onClick={() => setShowSNSModal(true)} 
            type="button"
          >
            + SNS 추가
          </button>
        </div>
        {formData.sns.length > 0 && (
          <div className="sns-list">
            {formData.sns.map((sns) => (
              <div key={sns.id} className="sns-item">
                <span>{sns.type}: </span>
                <a 
                  href={sns.url}
                  onClick={(e) => handleSNSClick(sns.url, e)}
                  className="sns-link"
                >
                  {sns.url}
                </a>
                <button 
                  onClick={() => handleDeleteSNS(sns.id)}
                  className="btn-delete"
                  type="button"
                >
                  삭제 ×
                </button>
              </div>
            ))}
          </div>
        )}
      </div>

      {/* 모달 컴포넌트들 */}
      <PhotoModal
        isOpen={selectedPhotoIndex !== null}
        photos={formData.photos}
        selectedIndex={selectedPhotoIndex}
        mainPhotoId={mainPhotoId}
        onClose={() => setSelectedPhotoIndex(null)}
        onPrevious={handleModalPrevious}
        onNext={handleModalNext}
        onSetMain={handleSetMainPhoto}
      />

      <CertificateModal
        isOpen={showCertificateModal}
        onClose={() => setShowCertificateModal(false)}
        onConfirm={handleCertificateConfirm}
      />

      <SNSModal
        isOpen={showSNSModal}
        onClose={() => setShowSNSModal(false)}
        onConfirm={handleSNSConfirm}
      />
    </div>
  );
}

export default ProfileTab;