// src/pages/c-join/c-join.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './c-join.css';

function CJoin() {
  const navigate = useNavigate();
  
  // 폼 데이터 상태 관리
  const [formData, setFormData] = useState({
    // 파트너 유형
    partnerType: 'carCenter', // carCenter or inspection
    
    // 로그인 정보
    businessNumber: '',
    password: '',
    passwordConfirm: '',
    
    // 대표자 및 업체 정보
    companyName: '',
    ceoName: '',
    ceoPhone: '',
    companyPhone: '',
    email: '',
    address: '',
    addressDetail: '',
    
    // 계좌 정보
    bankName: '',
    accountNumber: '',
    
    // 약관 동의
    agreeAll: false,
    agreeTerms: false,
    agreePrivacy: false,
    agreeLocation: false,
    agreeMarketing: false
  });

  // 사업자등록번호 포맷팅
  const formatBusinessNumber = (value) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (numbers.length <= 3) return numbers;
    if (numbers.length <= 5) return `${numbers.slice(0, 3)}-${numbers.slice(3)}`;
    return `${numbers.slice(0, 3)}-${numbers.slice(3, 5)}-${numbers.slice(5, 10)}`;
  };

  // 전화번호 포맷팅
  const formatPhoneNumber = (value) => {
    const numbers = value.replace(/[^\d]/g, '');
    if (numbers.length <= 3) return numbers;
    if (numbers.length <= 7) return `${numbers.slice(0, 3)}-${numbers.slice(3)}`;
    return `${numbers.slice(0, 3)}-${numbers.slice(3, 7)}-${numbers.slice(7, 11)}`;
  };

  // 입력 핸들러
  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    
    if (name === 'businessNumber') {
      setFormData(prev => ({ ...prev, [name]: formatBusinessNumber(value) }));
    } else if (name === 'ceoPhone' || name === 'companyPhone') {
      setFormData(prev => ({ ...prev, [name]: formatPhoneNumber(value) }));
    } else if (type === 'checkbox') {
      setFormData(prev => ({ ...prev, [name]: checked }));
    } else {
      setFormData(prev => ({ ...prev, [name]: value }));
    }
  };

  // 전체 동의 처리
  const handleAgreeAll = (checked) => {
    setFormData(prev => ({
      ...prev,
      agreeAll: checked,
      agreeTerms: checked,
      agreePrivacy: checked,
      agreeLocation: checked,
      agreeMarketing: checked
    }));
  };

  // 주소 검색 (다음 주소 API 연동 예정)
  const handleAddressSearch = () => {
    alert('주소 검색 기능은 추후 구현 예정입니다.');
  };

  // 사업자등록번호 인증
  const handleBusinessVerify = () => {
    alert('사업자등록번호 인증 기능은 추후 구현 예정입니다.');
  };

  // 계좌 인증
  const handleAccountVerify = () => {
    alert('계좌 인증 기능은 추후 구현 예정입니다.');
  };

  // 폼 제출
  const handleSubmit = (e) => {
    e.preventDefault();
    
    // 비밀번호 확인
    if (formData.password !== formData.passwordConfirm) {
      alert('비밀번호가 일치하지 않습니다.');
      return;
    }
    
    // 필수 약관 동의 확인
    if (!formData.agreeTerms || !formData.agreePrivacy) {
      alert('필수 약관에 동의해주세요.');
      return;
    }
    
    console.log('회원가입 데이터:', formData);
    
    // 가입 완료 알럿
    alert('가입 신청이 완료되었습니다.\n\n차바 관리자 심사 후, 영업일 기준 1~2일 내에\n문자로 결과를 안내해 드립니다.');
    
    // 로그인 페이지로 이동
    navigate('/c-log');
  };

  return (
    <div className="c-join-wrapper">
      <div className="c-join-container">
        <div className="c-join-header">
          <h1 onClick={() => navigate('/c-log')}>차바 파트너</h1>
          <h2>회원가입</h2>
        </div>

        <form className="c-join-form" onSubmit={handleSubmit}>
          {/* 파트너 유형 선택 */}
          <div className="form-section">
            <h3>파트너 유형</h3>
            <select 
              name="partnerType" 
              value={formData.partnerType}
              onChange={handleChange}
              className="form-select"
            >
              <option value="carCenter">카센터</option>
              <option value="inspection">검사소</option>
            </select>
          </div>

          {/* 로그인 정보 */}
          <div className="form-section">
            <h3>로그인 정보</h3>
            
            <div className="form-group with-button">
              <label>사업자등록번호</label>
              <div className="input-with-button">
                <input
                  type="text"
                  name="businessNumber"
                  value={formData.businessNumber}
                  onChange={handleChange}
                  placeholder="123-45-67890"
                  maxLength="12"
                  required
                />
                <button type="button" onClick={handleBusinessVerify} className="btn-verify">
                  인증하기
                </button>
              </div>
            </div>

            <div className="form-group">
              <label>비밀번호</label>
              <input
                type="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                placeholder="비밀번호를 입력하세요"
                required
              />
            </div>

            <div className="form-group">
              <label>비밀번호 확인</label>
              <input
                type="password"
                name="passwordConfirm"
                value={formData.passwordConfirm}
                onChange={handleChange}
                placeholder="비밀번호를 다시 입력하세요"
                required
              />
            </div>
          </div>

          {/* 대표자 및 업체 정보 */}
          <div className="form-section">
            <h3>대표자 및 업체 정보</h3>
            
            <div className="form-group">
              <label>상호명</label>
              <input
                type="text"
                name="companyName"
                value={formData.companyName}
                onChange={handleChange}
                placeholder="(주)차바모터스"
                required
              />
            </div>

            <div className="form-group">
              <label>대표자명</label>
              <input
                type="text"
                name="ceoName"
                value={formData.ceoName}
                onChange={handleChange}
                placeholder="홍길동"
                required
              />
            </div>

            <div className="form-group">
              <label>대표 전화번호</label>
              <input
                type="tel"
                name="ceoPhone"
                value={formData.ceoPhone}
                onChange={handleChange}
                placeholder="010-1234-5678"
                maxLength="13"
                required
              />
            </div>

            <div className="form-group">
              <label>업체 전화번호</label>
              <input
                type="tel"
                name="companyPhone"
                value={formData.companyPhone}
                onChange={handleChange}
                placeholder="02-1234-5678"
                maxLength="13"
                required
              />
            </div>

            <div className="form-group">
              <label>이메일</label>
              <input
                type="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                placeholder="ceo@chabamotor.com"
                required
              />
            </div>

            <div className="form-group with-button">
              <label>주소</label>
              <div className="input-with-button">
                <input
                  type="text"
                  name="address"
                  value={formData.address}
                  onChange={handleChange}
                  placeholder="주소를 검색하세요"
                  readOnly
                  required
                />
                <button type="button" onClick={handleAddressSearch} className="btn-search">
                  주소검색
                </button>
              </div>
              <input
                type="text"
                name="addressDetail"
                value={formData.addressDetail}
                onChange={handleChange}
                placeholder="상세주소를 입력하세요"
                className="mt-8"
              />
            </div>
          </div>

          {/* 계좌 정보 */}
          <div className="form-section">
            <h3>계좌 정보</h3>
            
            <div className="form-group">
              <label>은행</label>
              <select 
                name="bankName" 
                value={formData.bankName}
                onChange={handleChange}
                className="form-select"
                required
              >
                <option value="">은행을 선택하세요</option>
                <option value="kb">국민은행</option>
                <option value="shinhan">신한은행</option>
                <option value="woori">우리은행</option>
                <option value="hana">하나은행</option>
                <option value="nh">농협은행</option>
              </select>
            </div>

            <div className="form-group with-button">
              <label>계좌번호</label>
              <div className="input-with-button">
                <input
                  type="text"
                  name="accountNumber"
                  value={formData.accountNumber}
                  onChange={handleChange}
                  placeholder="123-456-789012"
                  required
                />
                <button type="button" onClick={handleAccountVerify} className="btn-verify">
                  인증하기
                </button>
              </div>
            </div>
          </div>

          {/* 업체 서류 */}
          <div className="form-section">
            <h3>업체 서류</h3>
            
            <div className="form-group">
              <label>사업자등록증</label>
              <input type="file" accept=".pdf,.jpg,.jpeg,.png" />
            </div>

            <div className="form-group">
              <label>면허/등록증</label>
              <input type="file" accept=".pdf,.jpg,.jpeg,.png" />
            </div>
          </div>

          {/* 약관 동의 */}
          <div className="form-section">
            <h3>약관 동의</h3>
            
            <div className="agree-all">
              <label>
                <input
                  type="checkbox"
                  checked={formData.agreeAll}
                  onChange={(e) => handleAgreeAll(e.target.checked)}
                />
                <span>전체 동의</span>
              </label>
            </div>

            <div className="agree-list">
              <label>
                <input
                  type="checkbox"
                  name="agreeTerms"
                  checked={formData.agreeTerms}
                  onChange={handleChange}
                />
                <span>[필수] 이용약관</span>
                <button type="button" className="btn-link" onClick={() => alert('이용약관 내용')}>
                  보기
                </button>
              </label>

              <label>
                <input
                  type="checkbox"
                  name="agreePrivacy"
                  checked={formData.agreePrivacy}
                  onChange={handleChange}
                />
                <span>[필수] 개인정보 수집 및 이용</span>
                <button type="button" className="btn-link" onClick={() => alert('개인정보 수집 및 이용 내용')}>
                  보기
                </button>
              </label>

              <label>
                <input
                  type="checkbox"
                  name="agreeLocation"
                  checked={formData.agreeLocation}
                  onChange={handleChange}
                />
                <span>[선택] 위치 기반 서비스</span>
                <button type="button" className="btn-link" onClick={() => alert('위치 기반 서비스 내용')}>
                  보기
                </button>
              </label>

              <label>
                <input
                  type="checkbox"
                  name="agreeMarketing"
                  checked={formData.agreeMarketing}
                  onChange={handleChange}
                />
                <span>[선택] 광고 정보 수신</span>
                <button type="button" className="btn-link" onClick={() => alert('광고 정보 수신 내용')}>
                  보기
                </button>
              </label>
            </div>
          </div>

          {/* 버튼 */}
          <div className="form-buttons">
            <button type="button" onClick={() => navigate('/c-log')} className="btn-cancel">
              취소
            </button>
            <button type="submit" className="btn-submit">
              가입하기
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default CJoin;