// src/pages/c-store-03/c-store-03.js
import React, { useState } from 'react';
import './c-store-03.css';

// 지역 데이터 import
import { REGION_DATA } from '../../data/regionData';

function CStore03() {
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false);
  const [capacity, setCapacity] = useState(5);
  const [selectedSido, setSelectedSido] = useState('');
  const [selectedGugun, setSelectedGugun] = useState([]);
  const [selectedRegions, setSelectedRegions] = useState([]);

  // 시/도 목록
  const sidoList = Object.keys(REGION_DATA);

  // 선택된 시/도에 따른 구/군 목록
  const gugunList = selectedSido ? Object.keys(REGION_DATA[selectedSido]) : [];

  // 시간당 가능 대수 변경
  const handleCapacityChange = (e) => {
    const value = parseInt(e.target.value) || 0;
    setCapacity(Math.max(0, value));
    setHasUnsavedChanges(true);
  };

  // 시/도 선택
  const handleSidoSelect = (sido) => {
    setSelectedSido(sido);
    setSelectedGugun([]);
  };

  // 구/군 선택
  const handleGugunToggle = (gugun) => {
    const newSelectedGugun = selectedGugun.includes(gugun)
      ? selectedGugun.filter(g => g !== gugun)
      : [...selectedGugun, gugun];
    
    setSelectedGugun(newSelectedGugun);
  };

  // 동 선택
  const handleDongToggle = (gugun, dong) => {
    const regionId = `${selectedSido}-${gugun}-${dong}`;
    const existingRegion = selectedRegions.find(r => r.id === regionId);
    
    if (existingRegion) {
      // 이미 선택된 경우 제거
      setSelectedRegions(selectedRegions.filter(r => r.id !== regionId));
    } else {
      // 새로 추가
      const newRegion = {
        id: regionId,
        display: `${gugun} > ${dong}`,
        sido: selectedSido,
        gugun: gugun,
        dong: dong
      };
      setSelectedRegions([...selectedRegions, newRegion]);
    }
    
    setHasUnsavedChanges(true);
  };

  // 전체 선택 토글
  const handleSelectAllDong = (gugun) => {
    const allRegionId = `${selectedSido}-${gugun}-전체`;
    const existingAllRegion = selectedRegions.find(r => r.id === allRegionId);
    
    if (existingAllRegion) {
      // 전체 선택 해제
      setSelectedRegions(selectedRegions.filter(r => r.id !== allRegionId));
    } else {
      // 전체 선택 추가 및 개별 동 제거
      const filteredRegions = selectedRegions.filter(r => 
        !(r.sido === selectedSido && r.gugun === gugun)
      );
      const newRegion = {
        id: allRegionId,
        display: `${gugun} > 전체`,
        sido: selectedSido,
        gugun: gugun,
        dong: '전체'
      };
      setSelectedRegions([...filteredRegions, newRegion]);
    }
    
    setHasUnsavedChanges(true);
  };

  // 선택된 지역인지 확인
  const isRegionSelected = (gugun, dong) => {
    return selectedRegions.some(r => 
      r.sido === selectedSido && r.gugun === gugun && (r.dong === dong || r.dong === '전체')
    );
  };

  // 구/군의 전체가 선택되었는지 확인
  const isGugunAllSelected = (gugun) => {
    return selectedRegions.some(r => 
      r.sido === selectedSido && r.gugun === gugun && r.dong === '전체'
    );
  };

  // 선택된 지역 삭제
  const handleRemoveRegion = (regionId) => {
    setSelectedRegions(selectedRegions.filter(r => r.id !== regionId));
    setHasUnsavedChanges(true);
  };

  // 저장
  const handleSave = () => {
    const saveData = {
      capacity,
      selectedRegions,
      timestamp: new Date().toISOString()
    };
    
    console.log('저장 데이터:', saveData);
    
    // 실제로는 API 호출
    // await api.saveCoverageSettings(saveData);
    
    alert('저장이 완료되었습니다.');
    setHasUnsavedChanges(false);
  };

  return (
    <div className="c-store-03">
      <div className="page-header">
        <h1>대행 커버리지 설정</h1>
        <button 
          className="btn-save"
          onClick={handleSave}
          disabled={!hasUnsavedChanges}
        >
          저장
        </button>
      </div>

      <div className="content-container">
        {/* 카센터 검사 대행 운영 설정 */}
        <div className="section">
          <h2 className="section-title">카센터 검사 대행 운영 설정</h2>
          <p className="section-description">
            * 검사 대행을 하지 않으시는 경우는 가능 차량 수를 '0'으로 만들어 주세요.
          </p>
          
          <div className="capacity-setting">
            <label htmlFor="capacity-input">시간당 가능 대수:</label>
            <input
              id="capacity-input"
              type="number"
              className="capacity-input"
              value={capacity}
              onChange={handleCapacityChange}
              min="0"
              max="99"
            />
            <span className="capacity-unit">대 / 시간</span>
          </div>
        </div>

        <hr className="section-divider" />

        {/* 대행 가능 지역 설정 */}
        <div className="section">
          <h2 className="section-title">대행 가능 지역 설정</h2>
          <p className="section-description">
            * 위에서 '0'대 이상으로 설정한 서비스에 적용될 지역입니다.
          </p>

          <div className="region-selector">
            {/* 시/도 선택 */}
            <div className="region-column">
              <div className="region-column-header">시/도</div>
              <div className="region-list">
                {sidoList.map(sido => (
                  <div
                    key={sido}
                    className={`region-item ${selectedSido === sido ? 'selected' : ''}`}
                    onClick={() => handleSidoSelect(sido)}
                    role="button"
                    tabIndex={0}
                    onKeyPress={(e) => {
                      if (e.key === 'Enter' || e.key === ' ') {
                        handleSidoSelect(sido);
                      }
                    }}
                  >
                    <label>{sido}</label>
                  </div>
                ))}
              </div>
            </div>

            {/* 시/군/구 선택 */}
            <div className="region-column">
              <div className="region-column-header">시/군/구</div>
              <div className="region-list">
                {selectedSido ? (
                  gugunList.map(gugun => (
                    <div
                      key={gugun}
                      className={`region-item ${selectedGugun.includes(gugun) ? 'selected' : ''}`}
                    >
                      <input
                        type="checkbox"
                        id={`gugun-${gugun}`}
                        checked={selectedGugun.includes(gugun)}
                        onChange={() => handleGugunToggle(gugun)}
                      />
                      <label htmlFor={`gugun-${gugun}`}>{gugun}</label>
                    </div>
                  ))
                ) : (
                  <div className="empty-message">시/도를 먼저 선택해주세요</div>
                )}
              </div>
            </div>

            {/* 읍/면/동 선택 */}
            <div className="region-column">
              <div className="region-column-header">읍/면/동</div>
              <div className="region-list">
                {selectedGugun.length > 0 ? (
                  <>
                    {selectedGugun.map(gugun => (
                      <div key={`${gugun}-group`}>
                        {/* 전체 선택 옵션 */}
                        <div
                          className={`region-item ${isGugunAllSelected(gugun) ? 'selected' : ''}`}
                          style={{ backgroundColor: '#f0f2ff', fontWeight: 600 }}
                        >
                          <input
                            type="checkbox"
                            id={`${gugun}-all`}
                            checked={isGugunAllSelected(gugun)}
                            onChange={() => handleSelectAllDong(gugun)}
                          />
                          <label htmlFor={`${gugun}-all`}>{gugun} 전체</label>
                        </div>
                        
                        {/* 개별 동 목록 */}
                        {!isGugunAllSelected(gugun) && 
                          REGION_DATA[selectedSido][gugun].map(dong => (
                            <div
                              key={`${gugun}-${dong}`}
                              className={`region-item ${isRegionSelected(gugun, dong) ? 'selected' : ''}`}
                              style={{ paddingLeft: '24px' }}
                            >
                              <input
                                type="checkbox"
                                id={`dong-${gugun}-${dong}`}
                                checked={isRegionSelected(gugun, dong)}
                                onChange={() => handleDongToggle(gugun, dong)}
                              />
                              <label htmlFor={`dong-${gugun}-${dong}`}>{dong}</label>
                            </div>
                          ))
                        }
                      </div>
                    ))}
                  </>
                ) : (
                  <div className="empty-message">시/군/구를 먼저 선택해주세요</div>
                )}
              </div>
            </div>

            {/* 선택된 지역 */}
            <div className="region-column selected-regions-column">
              <div className="region-column-header">
                선택된 지역 ({selectedRegions.length})
              </div>
              <div className="region-list">
                {selectedRegions.length > 0 ? (
                  selectedRegions.map(region => (
                    <div key={region.id} className="selected-region-tag">
                      <span>{region.display}</span>
                      <button
                        onClick={() => handleRemoveRegion(region.id)}
                        aria-label={`${region.display} 삭제`}
                        type="button"
                      >
                        ×
                      </button>
                    </div>
                  ))
                ) : (
                  <div className="empty-message">선택된 지역이 없습니다</div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CStore03;