import React, { useState } from 'react';

function CertificateModal({ isOpen, onClose, onConfirm }) {
  const [certificateData, setCertificateData] = useState({
    name: '',
    number: ''
  });

  if (!isOpen) return null;

  const handleSubmit = (e) => {
    e.preventDefault();
    if (certificateData.name.trim() && certificateData.number.trim()) {
      onConfirm(certificateData);
      setCertificateData({ name: '', number: '' });
    } else {
      alert('자격증명과 번호를 모두 입력해주세요.');
    }
  };

  const handleClose = () => {
    setCertificateData({ name: '', number: '' });
    onClose();
  };

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>자격증 추가</h3>
          <button className="modal-close" onClick={handleClose}>
            ×
          </button>
        </div>
        
        <form onSubmit={handleSubmit}>
          <div className="modal-body">
            <div className="form-group">
              <label>자격증을 입력해주세요</label>
              <input
                type="text"
                value={certificateData.name}
                onChange={(e) => setCertificateData({...certificateData, name: e.target.value})}
                placeholder="예: 자동차정비기능사"
                required
              />
            </div>
            
            <div className="form-group">
              <label>자격증 번호를 입력해주세요</label>
              <input
                type="text"
                value={certificateData.number}
                onChange={(e) => setCertificateData({...certificateData, number: e.target.value})}
                placeholder="예: 12-34-567890"
                required
              />
            </div>
          </div>
          
          <div className="modal-footer">
            <button type="button" className="btn-cancel" onClick={handleClose}>
              취소
            </button>
            <button type="submit" className="btn-confirm">
              확인
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default CertificateModal;