// src/pages/c-pay-01/c-pay-01.js
import React, { useState, useEffect } from 'react';
import { useLocation } from 'react-router-dom';
import DateModal from '../../components/DateModal/DateModal';
import './c-pay-01.css';

function CPay01() {
  const location = useLocation();
  
  // 파트너 타입 상태 관리
  const [partnerType, setPartnerType] = useState(localStorage.getItem('partnerType') || 'carCenter');
  
  // 현재 선택된 년월
  const [selectedDate, setSelectedDate] = useState({
    year: location.state?.year || 2025,
    month: location.state?.month || 7
  });
  
  // 모달 표시 여부
  const [showDateModal, setShowDateModal] = useState(false);

  // localStorage 변경 감지 (파트너 타입 전환 시)
  useEffect(() => {
    const handleStorageChange = () => {
      const newType = localStorage.getItem('partnerType') || 'carCenter';
      if (newType !== partnerType) {
        setPartnerType(newType);
      }
    };

    // storage 이벤트 리스너 추가
    window.addEventListener('storage', handleStorageChange);
    
    // 같은 탭에서의 변경도 감지하기 위한 커스텀 이벤트
    window.addEventListener('partnerTypeChanged', handleStorageChange);
    
    return () => {
      window.removeEventListener('storage', handleStorageChange);
      window.removeEventListener('partnerTypeChanged', handleStorageChange);
    };
  }, [partnerType]);

  // 더미 데이터 (실제로는 API에서 가져옴)
  const settlementData = {
    carCenter: {
      receivable: 1050000, // 받을 돈
      payable: 0, // 줄 돈
      transactions: [
        { 
          date: '07-28', 
          customer: '김OO', 
          vehicle: '1234',
          service: '엔진오일 에코', 
          payment: '모바일',
          amount: 80000, 
          fee: -4000, 
          net: 76000 
        },
        { 
          date: '07-25', 
          customer: '이XX',
          vehicle: '5678', 
          service: '브레이크 패드', 
          payment: '모바일',
          amount: 120000, 
          fee: -6000, 
          net: 114000 
        },
        { 
          date: '07-23', 
          customer: '박XX',
          vehicle: '9012', 
          service: '에어컨 필터', 
          payment: '현장',
          amount: 25000, 
          fee: 0, 
          net: 25000 
        },
        { 
          date: '07-20', 
          customer: '최OO',
          vehicle: '3456', 
          service: '타이어 교체', 
          payment: '모바일',
          amount: 280000, 
          fee: -14000, 
          net: 266000 
        },
        { 
          date: '07-18', 
          customer: '정XX',
          vehicle: '7890', 
          service: '정기점검', 
          payment: '현장',
          amount: 50000, 
          fee: 0, 
          net: 50000 
        },
      ]
    },
    inspection: {
      visitCount: 50,
      receivable: 25000,
      payable: 72500,
      transactions: [
        { 
          date: '07-28', 
          customer: '김OO',
          vehicle: '1234', 
          service: '정기검사',
          payment: '모바일', 
          amount: 25000,
          fee: 0,
          net: 25000
        },
        { 
          date: '07-28', 
          customer: '이XX',
          vehicle: '5678', 
          service: '종합검사',
          payment: '현장', 
          amount: 0,
          fee: -1450,
          net: -1450
        },
        { 
          date: '07-27', 
          customer: '박XX',
          vehicle: '9012', 
          service: '정기검사',
          payment: '현장', 
          amount: 0,
          fee: -1450,
          net: -1450
        },
        { 
          date: '07-26', 
          customer: '최OO',
          vehicle: '3456', 
          service: '정기검사',
          payment: '모바일', 
          amount: 0,
          fee: 0,
          net: 0
        },
        { 
          date: '07-25', 
          customer: '정XX',
          vehicle: '7890', 
          service: '종합검사',
          payment: '현장', 
          amount: 0,
          fee: -1450,
          net: -1450
        },
      ]
    }
  };

  // 날짜 포맷팅
  const formatDate = () => {
    return `${selectedDate.year}년 ${selectedDate.month}월`;
  };

  // 날짜 선택 모달 열기
  const openDateModal = () => {
    setShowDateModal(true);
  };

  // 날짜 조회 - DateModal에서 확인 시
  const handleDateConfirm = (year, month) => {
    setSelectedDate({ year, month });
    setShowDateModal(false);
    // 실제로는 여기서 API 호출
    console.log(`${year}년 ${month}월 데이터 조회`);
  };

  // 엑셀 다운로드
  const handleExcelDownload = () => {
    alert('이카운트 양식으로 다운로드합니다.');
    // 실제로는 엑셀 다운로드 로직
  };

  // 금액 포맷팅
  const formatAmount = (amount) => {
    if (amount === 0) return '-';
    return amount.toLocaleString('ko-KR');
  };

  // 현재 파트너 타입에 따른 데이터
  const currentData = settlementData[partnerType];

  return (
    <div className="settlement-container">
      {/* 헤더 */}
      <div className="settlement-header">
        <h2>정산 내역</h2>
        <button className="excel-download-btn" onClick={handleExcelDownload}>
          이카운트 양식 다운로드
        </button>
      </div>

      {/* 날짜 선택 */}
      <div className="date-selector">
        <button className="date-button" onClick={openDateModal}>
          {formatDate()} ▼
        </button>
      </div>

      {/* 정산 요약 카드 */}
      <div className="settlement-summary">
        <h3>{selectedDate.month}월 정산 요약</h3>
        <div className="summary-card">
          {partnerType === 'inspection' && (
            <div className="summary-item total">
              <span className="label">[방문완료 총 {currentData.visitCount} 건]</span>
            </div>
          )}
          
          <div className="summary-item">
            <span className="label">(+) 받을 돈 (모바일 결제 정산액):</span>
            <span className="amount positive">{formatAmount(currentData.receivable)} 원</span>
          </div>
          
          <div className="summary-item">
            <span className="label">(-) 줄 돈 (현장 결제 수수료):</span>
            <span className="amount negative">{formatAmount(currentData.payable)} 원</span>
          </div>
          
          <div className="summary-divider"></div>
          
          <div className="summary-total">
            {partnerType === 'carCenter' ? (
              <>
                <span className="label">▶ 최종적으로 차바가 {formatAmount(currentData.receivable - currentData.payable)} 원을 지급합니다.</span>
                <span className="desc">(정산 받을 금액)</span>
              </>
            ) : (
              <>
                <span className="label">▶ 최종적으로 파트너가 {formatAmount(currentData.payable - currentData.receivable)} 원을 지급해야 합니다.</span>
                <span className="desc">(정산 해줄 수수료)</span>
              </>
            )}
          </div>
        </div>
      </div>

      {/* 상세 거래 내역 */}
      <div className="transaction-details">
        <h3>{selectedDate.month}월 상세 거래 내역</h3>
        <div className="transaction-table">
          <table>
            <thead>
              <tr>
                <th>일자</th>
                <th>고객/차량</th>
                <th>서비스</th>
                <th>결제방식</th>
                <th>총액</th>
                <th>수수료</th>
                <th>정산액</th>
              </tr>
            </thead>
            <tbody>
              {currentData.transactions.map((transaction, index) => (
                <tr key={index}>
                  <td>{transaction.date}</td>
                  <td>{transaction.customer}/{transaction.vehicle}</td>
                  <td>{transaction.service}</td>
                  <td>
                    <span className={`payment-type ${transaction.payment === '모바일' ? 'mobile' : 'onsite'}`}>
                      {transaction.payment}
                    </span>
                  </td>
                  <td>{formatAmount(transaction.amount)}</td>
                  <td className={transaction.fee < 0 ? 'fee-negative' : ''}>
                    {formatAmount(transaction.fee)}
                  </td>
                  <td className={transaction.net > 0 ? 'net-positive' : transaction.net < 0 ? 'net-negative' : ''}>
                    {formatAmount(transaction.net)}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* 날짜 선택 모달 - 공통 컴포넌트 사용 */}
      <DateModal
        isOpen={showDateModal}
        onClose={() => setShowDateModal(false)}
        onConfirm={handleDateConfirm}
        initialYear={selectedDate.year}
        initialMonth={selectedDate.month}
        title="기간 선택"
        confirmText="조회하기"
        cancelText="취소"
      />
    </div>
  );
}

export default CPay01;