import React, { useState, useEffect, useRef } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import ProfileTab from './components/ProfileTab';
import BasicInfoTab from './components/BasicInfoTab';
import OperationTab from './components/OperationTab';
import SaveConfirmModal from './components/SaveConfirmModal';
import './c-store-01.css';

function CStore01() {
  const navigate = useNavigate();
  const location = useLocation();
  const [activeTab, setActiveTab] = useState('profile');
  const [hasUnsavedChanges, setHasUnsavedChanges] = useState(false);
  const [showSaveModal, setShowSaveModal] = useState(false);
  const [pendingNavigation, setPendingNavigation] = useState(null);
  const [partnerType, setPartnerType] = useState(
    localStorage.getItem('partnerType') || 'carCenter'
  );
  
  // 사용자가 이미 저장 안함을 선택했는지 추적
  const skipSaveRef = useRef(false);

  // 파트너 타입 변경 감지
  useEffect(() => {
    const handleStorageChange = () => {
      const newType = localStorage.getItem('partnerType') || 'carCenter';
      setPartnerType(newType);
    };

    window.addEventListener('storage', handleStorageChange);
    window.addEventListener('partnerTypeChanged', handleStorageChange);

    return () => {
      window.removeEventListener('storage', handleStorageChange);
      window.removeEventListener('partnerTypeChanged', handleStorageChange);
    };
  }, []);

  // 페이지 이탈 감지
  useEffect(() => {
    const handleBeforeUnload = (e) => {
      if (hasUnsavedChanges && !skipSaveRef.current) {
        e.preventDefault();
        e.returnValue = '';
      }
    };

    window.addEventListener('beforeunload', handleBeforeUnload);
    return () => window.removeEventListener('beforeunload', handleBeforeUnload);
  }, [hasUnsavedChanges]);

  // React Router 네비게이션 감지
  useEffect(() => {
    const handleNavigation = (e) => {
      const link = e.target.closest('a');
      if (!link) return;

      if (link.classList.contains('sns-link') || 
          link.getAttribute('target') === '_blank' ||
          link.href.startsWith('http')) {
        return;
      }
      
      if (hasUnsavedChanges && !skipSaveRef.current) {
        e.preventDefault();
        const href = link.getAttribute('href');
        if (href && href !== location.pathname) {
          setShowSaveModal(true);
          setPendingNavigation(href);
        }
      }
    };

    document.addEventListener('click', handleNavigation, true);

    return () => {
      document.removeEventListener('click', handleNavigation, true);
    };
  }, [hasUnsavedChanges, location.pathname]);

  // 데이터 변경 핸들러
  const handleDataChange = () => {
    setHasUnsavedChanges(true);
    skipSaveRef.current = false;
  };

  // 저장 핸들러
  const handleSave = () => {
    console.log('저장되었습니다.');
    setHasUnsavedChanges(false);
    skipSaveRef.current = false;
    alert('저장이 완료되었습니다.');
  };

  // 탭 변경 시 저장 확인
  const handleTabChange = (newTab) => {
    if (hasUnsavedChanges && !skipSaveRef.current) {
      const confirmSave = window.confirm('저장하지 않은 변경사항이 있습니다. 저장하시겠습니까?');
      if (confirmSave) {
        handleSave();
      } else {
        skipSaveRef.current = true;
      }
    }
    setActiveTab(newTab);
  };

  // 저장 확인 모달 핸들러
  const handleSaveCancel = () => {
    setShowSaveModal(false);
    setPendingNavigation(null);
  };

  const handleSaveConfirm = () => {
    handleSave();
    setShowSaveModal(false);
    if (pendingNavigation) {
      setTimeout(() => {
        navigate(pendingNavigation);
      }, 100);
    }
  };

  const handleSaveWithoutSaving = () => {
    setHasUnsavedChanges(false);
    skipSaveRef.current = true;
    setShowSaveModal(false);
    if (pendingNavigation) {
      setTimeout(() => {
        navigate(pendingNavigation);
      }, 100);
    }
  };

  return (
    <div className="c-store-01">
      <div className="page-header">
        <h1>내 {partnerType === 'carCenter' ? '카센터' : '검사소'} 정보</h1>
        <button 
          className="btn-save"
          onClick={handleSave}
          disabled={!hasUnsavedChanges}
        >
          저장
        </button>
      </div>

      <div className="tab-menu">
        <button
          className={`tab-item ${activeTab === 'profile' ? 'active' : ''}`}
          onClick={() => handleTabChange('profile')}
        >
          소개
        </button>
        <button
          className={`tab-item ${activeTab === 'basic' ? 'active' : ''}`}
          onClick={() => handleTabChange('basic')}
        >
          기본정보
        </button>
        <button
          className={`tab-item ${activeTab === 'operation' ? 'active' : ''}`}
          onClick={() => handleTabChange('operation')}
        >
          운영정보
        </button>
      </div>

      <div className="tab-content">
        {activeTab === 'profile' && (
          <ProfileTab onDataChange={handleDataChange} />
        )}
        {activeTab === 'basic' && (
          <BasicInfoTab onDataChange={handleDataChange} />
        )}
        {activeTab === 'operation' && (
          <OperationTab onDataChange={handleDataChange} />
        )}
      </div>

      <SaveConfirmModal
        isOpen={showSaveModal}
        onCancel={handleSaveCancel}
        onConfirm={handleSaveConfirm}
        onSkip={handleSaveWithoutSaving}
      />
    </div>
  );
}

export default CStore01;