// src/pages/c-cs-02/c-cs-02.js
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import './c-cs-02.css';

function CCs02() {
  const navigate = useNavigate();
  
  // 폼 데이터 상태 관리
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    phone: '',
    title: '',
    content: ''
  });

  // 입력값 변경 핸들러
  const handleChange = (e) => {
    const { name, value } = e.target;
    
    // 내용 필드는 500자 제한
    if (name === 'content' && value.length > 500) {
      return;
    }
    
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // 폼 제출 핸들러
  const handleSubmit = (e) => {
    e.preventDefault();
    
    // 필수 필드 검증
    if (!formData.email || !formData.title || !formData.content) {
      alert('필수 항목을 모두 입력해주세요.');
      return;
    }

    // 이메일 유효성 검증
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      alert('올바른 이메일 형식을 입력해주세요.');
      return;
    }

    // mailto 링크 생성
    const subject = encodeURIComponent(`[차바 파트너 문의] ${formData.title}`);
    const body = encodeURIComponent(
      `보내는 사람: ${formData.name}\n` +
      `이메일: ${formData.email}\n` +
      `전화번호: ${formData.phone}\n` +
      `\n---문의 내용---\n\n` +
      `${formData.content}`
    );
    
    const mailtoLink = `mailto:cs@chaba.co.kr?subject=${subject}&body=${body}`;
    
    // 이메일 클라이언트 열기
    window.location.href = mailtoLink;
    
    // 폼 초기화
    setFormData({
      name: '',
      email: '',
      phone: '',
      title: '',
      content: ''
    });
    
    // 성공 메시지
    setTimeout(() => {
      alert('문의가 접수되었습니다. 빠른 시일 내에 답변드리겠습니다.');
    }, 1000);
  };

  return (
    <div className="cs-container">
      <div className="cs-header">
        <button 
          className="back-button"
          onClick={() => navigate(-1)}
        >
          ← 뒤로가기
        </button>
        <h2>1:1 문의하기</h2>
        <div></div>
      </div>

      <form className="cs-form" onSubmit={handleSubmit}>
        <div className="form-section">
          <h3>보내는 사람</h3>
          
          <div className="form-group">
            <label htmlFor="name">성함</label>
            <input
              type="text"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleChange}
              placeholder="이름을 입력해주세요"
            />
          </div>

          <div className="form-group">
            <label htmlFor="email">
              이메일 <span className="required">*</span>
            </label>
            <input
              type="email"
              id="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              placeholder="example@email.com"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="phone">전화번호</label>
            <input
              type="tel"
              id="phone"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder="010-1234-5678"
            />
          </div>
        </div>

        <div className="form-section">
          <div className="form-group">
            <label htmlFor="title">
              제목 <span className="required">*</span>
            </label>
            <input
              type="text"
              id="title"
              name="title"
              value={formData.title}
              onChange={handleChange}
              placeholder="문의 제목을 입력해주세요"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="content">
              내용 <span className="required">*</span>
            </label>
            <textarea
              id="content"
              name="content"
              value={formData.content}
              onChange={handleChange}
              placeholder="자세한 내용을 입력해주세요"
              rows="8"
              required
            />
            <div className="char-count">
              {formData.content.length} / 500자
            </div>
          </div>
        </div>

        <button type="submit" className="submit-button">
          ✉️ 문의하기
        </button>
      </form>
    </div>
  );
}

export default CCs02;