import React, { useState } from 'react';

function SNSModal({ isOpen, onClose, onConfirm }) {
  const [snsData, setSnsData] = useState({
    type: '',
    url: ''
  });

  if (!isOpen) return null;

  // URL 유효성 검사 함수
  const isValidUrl = (urlString) => {
    try {
      new URL(urlString);
      return true;
    } catch {
      // 간단한 도메인 형식 체크
      const domainPattern = /^[a-zA-Z0-9][a-zA-Z0-9-]{0,61}[a-zA-Z0-9]?\.[a-zA-Z]{2,}$/;
      return domainPattern.test(urlString);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (snsData.type.trim() && snsData.url.trim()) {
      let finalUrl = snsData.url.trim();
      
      // http:// 또는 https://가 없으면 자동으로 추가
      if (!finalUrl.startsWith('http://') && !finalUrl.startsWith('https://')) {
        finalUrl = 'https://' + finalUrl;
      }
      
      // URL 유효성 체크
      if (!isValidUrl(finalUrl)) {
        alert('올바른 URL 형식을 입력해주세요. (예: caryak.net)');
        return;
      }
      
      onConfirm({
        type: snsData.type,
        url: finalUrl
      });
      setSnsData({ type: '', url: '' });
    } else {
      alert('SNS 서비스명과 URL을 모두 입력해주세요.');
    }
  };

  const handleClose = () => {
    setSnsData({ type: '', url: '' });
    onClose();
  };

  return (
    <div className="modal-overlay" onClick={handleClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        <div className="modal-header">
          <h3>SNS 추가</h3>
          <button className="modal-close" onClick={handleClose}>
            ×
          </button>
        </div>
        
        <form onSubmit={handleSubmit}>
          <div className="modal-body">
            <div className="form-group">
              <label>네이버, 유튜브등 SNS 서비스명을 입력해주세요</label>
              <input
                type="text"
                value={snsData.type}
                onChange={(e) => setSnsData({...snsData, type: e.target.value})}
                placeholder="예: 인스타그램"
                required
              />
            </div>
            
            <div className="form-group">
              <label>SNS url을 입력해주세요</label>
              <input
                type="text"
                value={snsData.url}
                onChange={(e) => setSnsData({...snsData, url: e.target.value})}
                placeholder="예: caryak.net 또는 https://caryak.net"
                required
              />
              <p className="help-text">http://나 https://는 자동으로 추가됩니다.</p>
            </div>
          </div>
          
          <div className="modal-footer">
            <button type="button" className="btn-cancel" onClick={handleClose}>
              취소
            </button>
            <button type="submit" className="btn-confirm">
              확인
            </button>
          </div>
        </form>
      </div>
    </div>
  );
}

export default SNSModal;
