import React from 'react';
import './VehicleNumberInput.css';

const VehicleNumberInput = ({ value = '', onChange, required = true }) => {
  const handleKeyUp = (e) => {
    let inputValue = e.target.value;
    
    // 숫자와 한글만 허용
    const cleaned = inputValue.replace(/[^0-9가-힣]/g, '');
    
    // 최대 8자리
    const limited = cleaned.slice(0, 8);
    
    // 값이 변경되었을 때만 업데이트
    if (limited !== inputValue) {
      e.target.value = limited;
    }
    
    onChange(limited);
  };

  return (
    <input
      type="text"
      className="form-input"
      placeholder="12가1234"
      defaultValue={value}
      onKeyUp={handleKeyUp}  // keyup 이벤트 사용
      onPaste={handleKeyUp}  // 붙여넣기도 처리
      required={required}
      autoComplete="off"
    />
  );
};

export default VehicleNumberInput;